import React from "react";

class Table extends React.Component {
    constructor(props){
        super(props);
    }
    componentDidMount(){}
    componentWillUnmount(){}
    componentDidUpdate(){}

    handleDelete = (index, e)=>{
        console.log("handleClick enter")
        this.props.onDelete(index);
        console.log("handleClick leave");
    };
    render(){
        return (
          <table>
            <thead>
              <tr>
                <th>id</th>
                <th>name</th>
                <th>gender</th>
                <th>pic</th>
                <th>action</th>
              </tr>
            </thead>
            <tbody>
              {this.props.rows.map(row => (
                <tr key={row.id}>
                    <td>{row.id}</td>
                  <td>
                    {row.name}
                  </td>
                  <td>{row.gender}</td>
                  <td><img src = {row.pic} alt="" /></td>
                  <td>
                    <button onClick={e => this.handleDelete(row.id, e)}>
                      delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        );
    }
}

export default Table;