import React from "react";
import Table from "./table"
import Form from "./form"
import Button from "./button";
import "es6-promise";
import "isomorphic-fetch";

class App extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            rows: []
        };
    }
    checkStatus= (response)=>{
        if(response.ok || (response.status >= 200 && response.status < 300)){
            return response;
        } else{
            let error = new Error(response.statusText);
            error.response = response;
            throw error;
        }
    }
    parseJson = (response)=>{
        return response.json();
    }
    load = ()=>{
        const url = "https://api.randomuser.me/?nat=us&gender=female&results=2";
        fetch(url,{
            method: 'GET',
            mode: 'no-cors'
        })
        .then(this.checkStatus)
        .then(this.parseJson)
        .then(body => {
            console.log(body);
            const { results } = body;
            let rows = results.map(row => {
              return {
                id: row.id.value,
                name:
                  row.name.title + " " + row.name.first + " " + row.name.last,
                gender: row.gender,
                pic: row.picture.large
              };
            });
            rows = this.state.rows.concat(rows);
            this.setState({ rows: rows });
        })
        .catch((e)=>{
              console.log("request fial", e);
        });
    }
    componentDidMount(){
        this.load();
    }
    componentDidUpdate(){}
    componentWillUnmount(){}
    handleDelete = (index)=>{
        const {rows} = this.state;
        this.setState({ rows: rows.filter((row) => {return row.id !== index;} )});
    }
    handleSubmit = (row)=>{
        let rows = this.state.rows.concat(row);
        this.setState({rows: rows});
    }
    render(){
        return (
            <div>
                <Table rows = {this.state.rows} onDelete = {this.handleDelete}/>
                <Form onSubmit = {this.handleSubmit}/>
                <Button onClick = {this.load}/>
            </div>
        );
    }
}

export default App;